.container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 30px;
}

.container_data {
  width: 800px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

@media only screen and (max-width: 900px) {
  .container_data {
    width: calc(100% - 140px);
  }
}

@media only screen and (max-width: 450px) {
  .container_data {
    width: calc(100% - 70px);
  }
}
