.header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    background-color: var(--first-color);
    opacity: 0.9;
}

.logo {
    font-size: 25px;
    color: var(--second-color);
}

.logo,
.menu_link {
    font-weight: bold;
}

.menu {
    display: flex;
    align-items: center;
    gap: 30px;
}

.menu_link {
    font-size: 17px;
    color: var(--third-color);
}

.menu_link:hover {
    color: var(--second-color);
}

.menu_language_item {
    margin: 0.25rem;
}

.menu_language_item_button {
    padding: 0px;
    border: none;
    background-color: transparent;
    transition: filter 0.4s ease 0s;
    filter: none;
    cursor: pointer;
}

@media only screen and (max-width: 900px) {
    .header {
        justify-content: center;
    }
    .menu {
        display: none;
    }
}
